body {
    font-family: 'Arial', sans-serif;
    background-color: #f3f3f3; /* Light gray background */
    color: #333; /* Dark gray text for readability */
    margin: 0;
    padding: 0;
  }
  
  h1, h2 {
    color: #faaa1b; /* Primary blue for headings */
    font-weight: bold;
  }
  
  button {
    background-color: #faaa1b; /* Primary blue for buttons */
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #e69500; /* Slightly darker blue on hover */
  }
  
  input[type="text"],
  input[type="email"],
  input[type="number"] {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 150px;
    transition: border-color 0.3s ease;
  }
  
  input:focus {
    border-color: #0072CE; /* Highlight input fields with primary blue */
    outline: none;
  }
  
  select {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: border-color 0.3s ease;
  }
  
  select:focus {
    border-color: #faaa1b;
    outline: none;
  }
  
  /* Layout Styles */
  .page-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .center-header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .form-row {
    display: flex;
    gap: 10px;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .button-spacing {
    margin-left: 10px;
  }
  
  .dogs-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  .dog-card {
    background: white;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 15px;
    width: 200px;
    text-align: left;
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  }
  
  .dog-card:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15); /* Enhanced shadow on hover */
  }
  
  .dog-card img {
    border-radius: 5px;
    max-width: 100%;
    height: 120px;
  }
  
  .center-items {
    display: flex;
    justify-content: center;
  }
  
  .pagination-controls {
    margin-top: 20px;
  }
  
  .matched-dog {
    margin-top: 20px;
    padding: 20px;
    background: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    text-align: center;
  }
  
  .matched-dog img {
    border-radius: 5px;
    max-width: 100%;
    height: auto;
  }

  .logo {
    width: 50px; /* Adjust the size as needed */
    height: auto; /* Maintain aspect ratio */
  }  
  header {
    text-align: center;
    margin-top: 20px;
  }